<template>
  <div class="team_list" >
    : 
    <div v-html="company_team">
      
      <ul>
        <li>
          <img src="http://file.ynet.com/2/1703/17/12551130.jpg">
          <div>
            <span></span>
            <p>
            1997101520
            </p>
          </div>
        </li>
        <li>
          <img src="http://file.ynet.com/2/1703/17/12551130.jpg">
          <div>
            <span></span>
            <p>
            1997101520
            </p>
          </div>
        </li>
        <li>
          <img src="http://file.ynet.com/2/1703/17/12551130.jpg">
          <div>
            <span></span>
            <p>
            1997101520
            </p>
          </div>
        </li>
      </ul>

    </div>
    
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data(){
      return{
        detailUrl: path + '/index/company/show?company_id=',
        company_team:""
      }
    },
    created(){


        var id = this.$route.params.id;
        //alert(id);


        //
        this.$http.get(this.detailUrl+id).then((response) => {
          
          var td = response.data
          this.company_team = td.company_team
          

        }).catch(function(response) {
        })

    }
  }
</script>

<style media="screen">
  .team_list{
    padding: 30px 20px;
  }
  .team_list ul li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 98%;
    padding: 10px 0;
    margin: auto;
    border-bottom: 1px solid #eef0f6;
  }
  .team_list ul li:last-child{
    border: none;
  }
  .team_list ul li img{
    width: 140px;
    height: 200px;
  }
  .team_list ul li div{
    width: 750px;
    color: #898989;
    font-size: 15px;
    line-height: 30px;
  }
  .team_list ul li div span{
    font-weight: bold;
    color:#616161;
  }
</style>



